<template>
  <!-- <nav>
    <router-link to="/">Home</router-link> |
    <router-link to="/about">About</router-link>
  </nav>
  <router-view/> -->
  <div class="main">
    <div class="main-wrapper">
      <div class="main-header">
      <div>Выберите сценарий:</div>
        <input type="number"
          v-model="testNumber">
        <button
          @click="createReport"
          >
          Сформировать
        </button>
      </div>

      <table>
        <tr class="table-header">
          <th>№</th>
          <th>title</th>
          <th>type</th>
          <th>Дата</th>
          <th>Время</th>
          <th>Номер попытки</th>
          <th>Верный ответ</th>
          <th>Текущий ответ</th>
          <th>fb stars</th>
          <th>fb Полезность</th>
          <th>fb Сложность</th>
          <th>fb Частотность</th>
          <th>fb Диагноз и лечение</th>
          <th>fb комментарий</th>
          <th>fb интересующаятема</th>
        </tr>

        <tr
          v-for="(answer,index) in testData"
          :key="index"
        >
          <td>{{index+1}}</td>
          <td>{{findQuestionById(answer.question_id)}}</td>
          <td>{{answer.body.description.type}}</td>
          <!-- <td>{{answer.created_at}}</td> -->
          <td>{{getDateFromISO(answer.created_at)}}</td>
          <td>{{getTimeFromISO(answer.created_at)}}</td>
          <td>{{answer.body.description.numberTry}}</td>
          <td>{{answer.body.description.isRight}}</td>
          <td>{{getCurrentAnswer(answer.question_id, answer.body.description.currentAnswer)}}</td>
          <td>{{getFeedbackItem(answer.body.description.feedback, "stars")}}</td>
          <td>{{getFeedbackItem(answer.body.description.feedback, "useful")}}</td>
          <td>{{getFeedbackItem(answer.body.description.feedback, "complexity")}}</td>
          <td>{{getFeedbackItem(answer.body.description.feedback, "frequency")}}</td>
          <td>{{getFeedbackItem(answer.body.description.feedback, "disagreeDiagnosis")}}</td>
          <td>{{getFeedbackItem(answer.body.description.feedback, "comment")}}</td>
          <td>{{getFeedbackItem(answer.body.description.feedback, "interestingCase")}}</td>
        </tr>
      </table>

    </div>
  </div>

</template>

<script>
// import reportHeader from '@/components/game/reportHeader.vue'
// import reportTable from '@/components/game/reportTable.vue'
import axios from 'axios'

export default {
  // components: {
  //   reportHeader,
  //   reportTable
  // }
    data() {
      return {
        testNumber: 1,
        testData: [],
        questionData: []
      }
    },
    // computed:{
    //   getFeedback(){

    //   }
    // },
    methods: {
      createReport(){
        return axios(`https://tech1.med-game.ru/api/v1/admin/test/${this.testNumber}/answers`, {
          method: "GET"
        })
          .then((testAnswers) => {

            this.testData = testAnswers.data.data.sort(function(a,b){
              return a.id - b.id;
            })

            this.writeNumberQuestion()

            // this.testData = testAnswers.data.data
          })          
          .catch((error) => {
            console.log('Ошибка:');
            console.log(error)
            this.testData = []
            return error
          })

      },

      writeNumberQuestion(){
        return axios(`https://tech1.med-game.ru/api/v1/admin/test/${this.testNumber}/questions`, {
          method: "GET"
        })
          .then((questions) => {
            this.questionData = questions.data.data
          })          
          .catch((error) => {
            console.log('Ошибка:');
            console.log(error)
            this.testData = []
            return error
          })
      },

      findQuestionById(question_id){
        let obj = this.questionData.find(o => o.id === question_id)
        if (obj !== undefined) {
          return obj.body.title
        }
        else{
          return ""
        }        
      },
      getCurrentAnswer(question_id, numberAnswer) {
        if (numberAnswer !== undefined) {
          let obj = this.questionData.find(o => o.id === question_id)
          if (obj !== undefined) {
            if (obj.body.title === "Диагноз"){
              if (obj.body.bodyButtons[numberAnswer] !== undefined) {
                return obj.body.bodyButtons[numberAnswer].buttonText
              }
              else{
                return numberAnswer
              }
              
            }
            else {
              return numberAnswer
            }
            
          }
          else{
            return ""
          }
        }
        else {
          return ""
        }
      },

      getFeedbackItem(feedback, item){
        if (feedback != undefined){
          
          if (item === "useful") {
            if (feedback[item]) {
              return "Да"
            }
            else {
              return "Нет"           
            }
          }
          
          if (item === "complexity") {
            if (feedback[item] === 0) {
              return "не выбрано"
            }
            else if (feedback[item] === 1) {
              return "Слишком сложно"           
            }
            else if (feedback[item] === 2) {
              return "Слишком просто"           
            }
            else{
              return ""
            }
          }

          if (item === "frequency") {
            if (feedback[item] === 0) {
              return "не выбрано"
            }
            else if (feedback[item] === 1) {
              return "Слишком редкая ситуация"           
            }
            else if (feedback[item] === 2) {
              return "Слишком частая ситуация"           
            }
            else{
              return ""
            }
          }

          if (item === "disagreeDiagnosis") {
            if (feedback[item]) {
              return "Не согласен с решением"
            }
            else {
              return "не выбрано"
            }
          }


          else{
            return feedback[item]
          }
        }
        else {
          return ""
        }
        
      },

      getDateFromISO(serverTime) {
        const d1 = new Date(serverTime)
        return d1.toLocaleDateString()
      },
      getTimeFromISO(serverTime) {
        const d1 = new Date(serverTime)
        return d1.toLocaleTimeString()
      }

    }    
}

</script>

<style>
  .main{
    display: flex;
    flex-direction: column;
  }
  .main-wrapper{
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
  }
  table{
    border-spacing: 0px;
    border-collapse: collapse;
  }
  td{
    border: 1px grey solid;
  }
  th{
    border: 1px black solid;
  }
</style>
